export default "\r\n* {\r\n  box-sizing: border-box;\r\n}\r\n\r\n/* Add a gray background color with some padding */\r\n\r\nbody {\r\n  font-family: Arial;\r\n  padding: 20px;\r\n  background: #f1f1f1;\r\n}\r\n\r\n/* Header/Blog Title */\r\n\r\n.header {\r\n  padding: 30px;\r\n  font-size: 40px;\r\n  text-align: center;\r\n  background: white;\r\n}\r\n\r\n/* Create two unequal columns that floats next to each other */\r\n\r\n/* Left column */\r\n\r\n.leftcolumn {   \r\n  float: left;\r\n  width: 75%;\r\n}\r\n\r\n/* Right column */\r\n\r\n.rightcolumn {\r\n  float: left;\r\n  width: 25%;\r\n  padding-left: 20px;\r\n}\r\n\r\n/* Add a card effect for articles */\r\n\r\n.card {\r\n   background-color: white;\r\n   padding: 20px;\r\n   margin-top: 20px;\r\n}\r\n\r\n/* Clear floats after the columns */\r\n\r\n.row:after {\r\n  content: \"\";\r\n  display: table;\r\n  clear: both;\r\n}\r\n\r\n/* Footer */\r\n\r\n.footer {\r\n  padding: 20px;\r\n  text-align: center;\r\n  background: #ddd;\r\n  margin-top: 20px;\r\n}\r\n\r\n.Supervisor{\r\n    text-align: left;\r\n    float:left;\r\n    padding-left:20px;\r\n}\r\n\r\n.Bill{\r\n    text-align: center;\r\n    float:center; \r\n       \r\n}\r\n\r\n.Location{\r\n    text-align: right;\r\n    float:right;\r\n    \r\n}\r\n\r\n/* Responsive layout - when the screen is less than 800px wide, make the two columns stack on top of each other instead of next to each other */\r\n\r\n@media screen and (max-width: 800px) {\r\n  .leftcolumn, .rightcolumn {   \r\n    width: 100%;\r\n    padding: 0;\r\n  }\r\n}\r\n\r\n/*# sourceMappingURL=data:application/json;base64, */"